import React from 'react';
import { Link } from 'react-router';

export default class GenresItem extends React.Component {
    render() {
        return (
            <Link to={this.props.link} title={this.props.name}>
                <span className = "genres-item">
                    {this.props.name}
                </span>
            </Link>
        );
    }
}



