import React from 'react';
import { Modal, Col, Row, Alert, MenuItem, DropdownButton } from 'react-bootstrap';

class PlayersBySeria extends React.Component {
    constructor(props) {
        super(props);
        this.handlePlayerChanged = this.handlePlayerChanged.bind(this);
    }

    handlePlayerChanged(e) {
        this.props.player_changed(e.target.value);
    }

    render() {
        return(
            <div className = "form-group">
                <select className = "form-control" onChange = {this.handlePlayerChanged} value = {this.props.selectedPlayer}>
                    {this.props.players.map((v, i) => (
                        <option key = {i} value = {i}>
                            {v.title}
                        </option>
                    ))}
                </select>
            </div>
        );
    }
}

class SeriesByType extends React.Component {
    constructor(props) {
        super(props);
        this.handleSeriaChanged = this.handleSeriaChanged.bind(this);
    }

    handleSeriaChanged(e) {
        this.props.seria_changed(parseInt(e.target.value));
    }

    render() {
        return(
            <div className = "form-group">
                <select className = "form-control" onChange = {this.handleSeriaChanged} value = {this.props.selectedNumber}>
                    {this.props.series.map((v, i) => (
                        <option key = {i} value = {i}>
                            {v.name}
                        </option>
                    ))}
                </select>
            </div>
        );
    }
}

class Types extends React.Component {
    constructor(props) {
        super(props);
        this.handleTypeChanged = this.handleTypeChanged.bind(this);
    }

    handleTypeChanged(e) {
        this.props.type_changed(e.target.value);
    }

    componentWillMount() {
        let types = [];
        for(let id in this.props.types) types.push(id);
        this.props.type_changed(types[0]);
    }

    render() {
        let types = [];
        for(let id in this.props.types)
            types.push(
                <option key = {id} value = {id}>
                    {this.props.types[id]}
                </option>
            );
        return(
            <div className = "form-group">
                <select className = "form-control" onChange = {this.handleTypeChanged} value = {this.props.selectedType}>
                    {types}
                </select>
            </div>
        );
    }
}

export default class MainHere extends React.Component {
    render() {
        if (this.props.series === 0) {
            return <div className = "alert alert-danger">Пока еще нет серий =(</div>;
        }
        try {
            const allTypes = this.props.series.types;
            const availableSeries = this.props.series.data.filter(v => this.props.state.activeType == v.type);
            let activeSeria = null;
            if (availableSeries.length) {
                activeSeria = availableSeries[this.props.state.activeNumber];
            }
            let availablePlayers = [];
            if (activeSeria) {
                availablePlayers = activeSeria.players;
            }
            let activePlayer = null;
            if (availablePlayers.length) {
                activePlayer = availablePlayers[this.props.state.activePlayer];
            }

            return(
                <div id="full-video" className = "row">
                    <ul id="navt" className="nav-tabs block-p col-sm-12 col-xs-12" role="tablist" style = {{borderBottom: "0"}}>
                        {
                            Object.keys(allTypes).length > 1 &&
                            <Types
                                type_changed = {this.props.actions.changeSeriesType}
                                types = {allTypes}
                                selectedType = {this.props.state.activeType}
                            />
                        }
                        {
                            availableSeries.length > 1 && 
                            <SeriesByType
                                series = {availableSeries}
                                seria_changed = {this.props.actions.changeSeriesNumber}
                                selectedNumber = {this.props.state.activeNumber}
                            />
                        }
                        {
                            availablePlayers.length > 1 && 
                            <PlayersBySeria
                                players = {availablePlayers}
                                player_changed = {this.props.actions.changeSeriesPlayer}
                                selectedPlayer = {this.props.state.activePlayer}
                            />
                        }
                        <div className = "embed-responsive embed-responsive-16by9">
                        {
                            activePlayer ?
                            <iframe allowFullScreen = {true} className = "embed-responsive-item" src = {activePlayer.url}></iframe>
                            :
                            <div className = "alert alert-danger">Нет плееров для данной серии.</div>
                        }
                        </div>
                    </ul>
                    <br />
                </div>
            );
        } catch(e) {
            console.error('Error in series.jsx', e);
            console.info(this.props);
            return (<div className = "alert alert-danger">Упс... Произогла ошибочка!</div>);
        }
    }
}



