import React from 'react';
import Item from './item';
import { Col, Row } from 'react-bootstrap';
import { Link } from 'react-router';

export default class Categories extends React.Component {
    render() {
        const { parentId, title, parentUri } = this.props;
        return (
            <div className="dropdown">
                <button className="btn btn-link nav-link dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    {title}
                </button>
                <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <Link to = {`/search/category/${parentUri}/`} className = "dropdown-item">
                        {title}
                    </Link>
                    <div className = "dropdown-divider"></div>
                    {this.props.genres.filter(v => v.parent == parentId).map((val, i) => (
                        <Link key = {i} to = {`/search/category/${val.altname}`} className = "dropdown-item">
                            {val.name}
                        </Link>
                    ))}
                </div>
            </div>
        );
    }
}


