import React from 'react';
import Shortstory from "../Shortstory";
import OwlCarousel from 'react-owl-carousel';
import { Link } from 'react-router';

export default class List extends React.Component {
    render() {
        let content = this.props.posts.map((val, i) => (
            <Shortstory key = {i} full_link = {"/post/"  + val.alt_name} title = {val.title} poster = {val.poster} />
        ));

        if (Array.isArray(this.props.posts) && this.props.posts.length > 0) {
            return(
                <section className="block">
                    <h1 className="main-header">
                        <Link to = {`/search/category/${this.props.category}`} title={this.props.name}>{this.props.name}</Link>
                    </h1>
                    <OwlCarousel
                        id = {this.props.id}
                        itemsCustom = {[
                            [0, 1],
                            [468, 2],
                            [768, 2],
                            [992, 4],
                            // [1400, 5]
                            // [1500, 4],
                            // [2000, 5],
                            // [2500, 6],
                            // [3000, 7],
                            // [3500, 8],
                            // [4000, 9],
                            // [4500, 10]
                        ]}
                        pagination = {false}
                        stopOnHover = {true}
                        navigation = {false}
                        navigationText = {[
                            "<i class='fa fa-angle-left'></i>",
                            "<i class='fa fa-angle-right'></i>"
                        ]}
                        autoPlay = {Math.random() * 2000 + 1000}>
                        {content}
                    </OwlCarousel>
                </section>
            );
        } else {
            return null;
        }
    }
}


