import { loadData, changeSearchField } from '../../actions';
import { RECEIVE_POSTS, FETCHING_POSTS } from '../../constants';
import Api from '../../lib/Api';
import { browserHistory } from 'react-router';

export function requestSearch(name, field, page = 1) {
    return dispatch => {
        const localKey = `Search/${field}/${name}/${page}`;
        const localData = Api.getLocalData(localKey);
        if (localData) {
            dispatch(smartPostsReceiving(localData, field));
        } else {
            const localDataOld = Api.getLocalData(localKey, true); // now let ignore deadline
            if (localDataOld) {
                dispatch(smartPostsReceiving(localDataOld, field));
            } else {
                dispatch(fetchingPosts());
            }
            Api.get(`search/${field}/${name}/${page}/12`)
                .then(r => {
                    if (!r.posts.length && page != 1) {
                        browserHistory.push(`/search/${field}/${name}/`);
                    } else {
                        dispatch(smartPostsReceiving(r, field));
                        Api.saveLocalData(localKey, r, 1/290);
                    }
                })
                .catch(e => {
                    if (localDataOld === null) {
                        browserHistory.push("/");
                    }
                });
        }
    };
}

export function smartPostsReceiving(posts, field) {
    return dispatch => {
        dispatch(receivePosts(posts));
        dispatch(changeSearchField(field));
    };
}

export function fetchingPosts() {
    return {
        type: FETCHING_POSTS,
    };
}

export function requestCategory(name, page = 1) {
    return requestSearch(name, 'category', page);
}

export function requestPosts(name, page = 1) {
    return requestSearch(name, 'name', page);
}

export function receivePosts(posts) {
    return {
        type: RECEIVE_POSTS,
        posts,
    };
}

export function resetPosts() {
    return {
        type: RECEIVE_POSTS,
        posts: [],
    };
}


