import React from 'react';
import { connect } from 'react-redux';
import PostsPage from '../../components/PostsPage';
import Loading from '../../components/Loading';
import { requestCategory, requestPosts } from './actions';

class PostsContainer extends React.Component {
    loadData(field, value, page_num) {
        if (this.props.posts.isFetching) {
            return null;
        }
        const posts = this.props.posts.data;
        if (field == 'category') {
            if (!posts || !posts.category || posts.category.alt_name != value || page_num != posts.currentPage) {
                this.props.dispatch(requestCategory(value, page_num));
            }
        } else {
            if (!posts || !posts.postName || posts.postName != value || page_num != posts.currentPage) {
                this.props.dispatch(requestPosts(value, page_num));
            }
        }
    }

    componentWillMount() {
        const field = this.props.params.field || '';
        const value = this.props.params.value || '';
        const pageNum = this.props.params.page_num || 1;
        this.loadData(field, value, pageNum);
    }

    componentWillUpdate(next) {
        const field = next.params.field || '';
        const value = next.params.value || '';
        const pageNum = next.params.page_num || 1;
        this.loadData(field, value, pageNum);
    }

    render() {
        if (this.props.posts.isFetching) {
            return <Loading />;
        }
        if (!this.props.posts.data) {
            return null;
        }
        return <PostsPage
            params = {{
                field: this.props.params.field,
                value: this.props.params.value
            }}
            data = {this.props.posts.data}
        />;
    }
}

export default connect((state) => ({ posts: state.posts }))(PostsContainer);



