import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as ActionCreators from './actions';
import QuickSearchForm from '../../components/QuickSearchForm';

class QuickSearchFormContainer extends React.Component {
    render() {
        const actionCreators = bindActionCreators(ActionCreators, this.props.dispatch);
        return <QuickSearchForm
            actions = {actionCreators}
            posts = {this.props.posts}
        />;
    }
}

export default connect((state) => ({ posts: state.QSF }))(QuickSearchFormContainer);


