import { loadData, changeSeriesNumber, changeSeriesPlayer, changeSeriesType } from '../../actions';
import { RECEIVE_POST, PAGES, FETCHING_POST } from '../../constants';
import Api from '../../lib/Api';
import { browserHistory } from 'react-router';

export function requestPost(altName) {
    const page = PAGES.POST;
    return dispatch => {
        const localKey = `Post/${altName}`;
        const localData = Api.getLocalData(localKey);
        if (localData) {
            dispatch(smartPostReceiving(localData));
        } else {
            const localDataOld = Api.getLocalData(localKey, true); // now let ignore deadline
            if (localDataOld) {
                dispatch(smartPostReceiving(localDataOld));
            } else {
                dispatch(fetchingPost());
            }
            Api.get(`post/alt_name/${altName}`)
                .then(r => {
                    dispatch(smartPostReceiving(r));
                    Api.saveLocalData(localKey, r, 1/290);
                })
                .catch(e => {
                    if (localDataOld === null) {
                        browserHistory.push("/");
                    }
                });
        }
    };
}

export function smartPostReceiving(post) {
    return dispatch => {
        let firstType = null;
        for(let id in post.series.types) {
            if (firstType === null) {
                firstType = id;
                break;
            }
        }
        dispatch(receivePost(post));
        dispatch(changeSeriesType(firstType));
        dispatch(changeSeriesNumber(0));
        dispatch(changeSeriesPlayer(0));
    };
}

export function fetchingPost() {
    return {
        type: FETCHING_POST,
    };
}

export function receivePost(post) {
    return {
        type: RECEIVE_POST,
        post,
    };
}

export function resetPost() {
    return {
        type: RECEIVE_POST,
        post: null,
    };
}


