import createReducer from '../../lib/createReducer';
import { RECEIVE_RANDOM_RELEASE } from '../../constants';

const initialState = {
    data: null,
    isLoaded: false,
};

let reducer = createReducer(initialState, {
    [RECEIVE_RANDOM_RELEASE]: (state, action) => {
        return {
            data: action.release,
            isLoaded: true,
        };
    }
}); 

export { initialState, reducer };


