import React from 'react';
import RegistrationBlock from './RegistrationBlock';
import LoginBlock from './LoginBlock';
import SocialsBlock from './SocialsBlock';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

export default class AuthModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdownOpen: false,
            window: 1,
        };
        this.toggle = this.toggle.bind(this);
        this.changeActiveWindow = this.changeActiveWindow.bind(this);
    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen,
        });
    }

    changeActiveWindow() {
        this.setState({
            window: !this.state.window,
        });
    }

    render() {
        return (
            <div id = "profile-block" className = "row">
                <div className = "col-xs-12 col-sm-12">
                    <h1>Личный кабинет</h1>
                </div>
                <div className = "col"></div>
                <div className = "col-xs-12 col-sm-12 col-md-10 col-lg-6">
                    <div className = "card">
                        <div className = "card-body">
                            <ul className="nav nav-tabs">
                                <li className="nav-item">
                                    <a className="nav-link active" id="login-tab" data-toggle="tab" href="#login" role="tab" aria-controls="login" aria-expanded="true">Вход</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" id="registration-tab" data-toggle="tab" href="#registration" role="tab" aria-controls="registration" aria-expanded="true">Регистрация</a>
                                </li>
                            </ul>
                            <br />
                            { this.props.errorMessage !== null ? (
                            <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                <button type="button" className="close" data-dismiss="alert" aria-label="Close" onClick = {this.props.clearErrorMessage}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                                {this.props.errorMessage}
                            </div>
                            ): null }
                            <div className="row">
                                <div className="col-xs-12 col-sm-12 col-md-7 vertical-divider-right">
                                    <div className="tab-content" id="myTabContent">
                                        <div className="tab-pane fade show active" id="login" role="tabpanel" aria-labelledby="login-tab">
                                            <LoginBlock login={this.props.login} />
                                        </div>
                                        <div className="tab-pane fade" id="registration" role="tabpanel" aria-labelledby="registration-tab">
                                            <RegistrationBlock register={this.props.register} />
                                        </div>
                                    </div>
                                </div>
                                <div className="col text-center">
                                    <div className="row mb-2">
                                        <div className="col">
                                            Или войти через:
                                        </div>
                                    </div>
                                    <SocialsBlock socialsAuth = {this.props.socialsAuth} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className = "col"></div>
            </div>
        );
    }
}


