import React from 'react';
import { connect } from 'react-redux';
import HomePage from '../../components/HomePage';
import { loadCategoriesFromHomePage } from './actions';

class HomePageContainer extends React.Component {
    render() {
        return <HomePage latestPosts = {this.props.latestPosts} />;
    }
}

export default connect((state) => ({ latestPosts: state.latestPosts }))(HomePageContainer);


