import React from 'react';
import { browserHistory, Link } from 'react-router';
import { IndexLinkContainer, LinkContainer } from 'react-router-bootstrap';
import { Grid, Row, Nav, NavItem } from 'react-bootstrap';
import Categories from '../../containers/Categories';
import QuickSearchForm from '../../containers/QuickSearchForm';
import Auth from '../../containers/Auth';

export default class TopMenu extends React.Component {
    render() {
        return (
            <nav id="top-menu" className="navbar navbar-expand-lg navbar-light bg-light">
                <div className = "container">
                    <Link to = "/" className = "navbar-brand">FireDub</Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <img width = "30px" src="/open-iconic/svg/menu.svg" alt="Меню" />
                    </button>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item"><Link className="nav-link" to = "/">Главная</Link></li>
                            <li className="nav-item"><Link className="nav-link" to = "/about/">О нас</Link></li>
                            <li className="nav-item">
                                <Categories title = "Аниме" parentUri = "anime" parentId = {2} />
                            </li>
                            <li className="nav-item">
                                <Categories title = "Дорамы" parentUri = "drama" parentId = {3} />
                            </li>
                            <li className="nav-item"><Link className="nav-link" to = "/search/category/films/">Фильмы онлайн</Link></li>
                            <li className="nav-item"><Link className="nav-link" to = "/profile/">Личный кабинет</Link></li>
                        </ul>
                        <QuickSearchForm />
                    </div>
                </div>
            </nav>
        );
    }
}


