import createReducer from '../../lib/createReducer';
import * as constants from './constants';

const initialState = {
    userData: null,
    errorMessage: null,
    isLogged: false,
    isRegistered: false,
};

let reducer = createReducer(initialState, {
    [constants.LOGIN.SUCCESS]: (state, action) => {
        return Object.assign({}, state, {
            userData: action.userData,
            errorMessage: null,
            isLogged: true,
        });
    },
    [constants.LOGIN.FAILED]: (state, action) => {
        return Object.assign({}, state, {
            userData: null,
            errorMessage: action.errorMessage,
            isLogged: false,
        });
    },
    [constants.REGISTER.SUCCESS]: (state, action) => {
        return Object.assign({}, state, {
            errorMessage: null,
            isRegistered: true,
        });
    },
    [constants.REGISTER.FAILED]: (state, action) => {
        return Object.assign({}, state, {
            errorMessage: action.errorMessage,
            isRegistered: false,
        });
    },
    [constants.LOGOUT.SUCCESS]: (state, action) => {
        return Object.assign({}, state, {
            errorMessage: null,
            isRegistered: false,
            isLogged: false,
        });
    },
    [constants.LOGOUT.FAILED]: (state, action) => {
        return Object.assign({}, state, {
            errorMessage: action.errorMessage,
        });
    },
    [constants.CLEAR_ERROR_MESSAGE]: (state, action) => {
        return Object.assign({}, state, {
            errorMessage: null,
        });
    },
});

export { initialState, reducer };


