import React from 'react';

export default class Left extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            showModal: false
        }
        this.open = this.open.bind(this);
        this.close = this.close.bind(this);
    }

    open(){
        this.setState({
            showModal: true
        });
    }

    close(){
        this.setState({
            showModal: false
        });
    }

    render(){
        const trailerContent = [
            <div className = "row">
                <div className = "col">
                    <button className = "btn btn-secondary btn-trailer btn-block" data-toggle="modal" data-target="#exampleModal">
                        <i className="fa fa-video-camera"></i><b>Смотреть трейлер</b>
                    </button>
                </div>
            </div>,
            <div className="modal fade" id="exampleModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog modal-lg" role="document">
                    <div className="modal-content">
                        <div className="modal-body">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe className="embed-responsive-item" src={this.props.trailer_url}></iframe>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        ];

        return(
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-4">

                <div className = "row">
                    <div className = "col">
                        <img src = {this.props.poster} width = "100%" />
                    </div>
                </div>
                
                { !!this.props.trailer_url && trailerContent }
                
            </div>
        );
    }
}


