import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as ActionCreators from './actions';
import Auth from '../../components/Auth';

class AuthContainer extends React.Component {
    render() {
        const actionCreators = bindActionCreators(ActionCreators, this.props.dispatch);
        let socialsAuth = {
            vk: actionCreators.loginViaVk,
        };
        return <Auth
            state = {this.props.auth}
            register = {actionCreators.register}
            login = {actionCreators.login}
            logout = {actionCreators.logout}
            clearErrorMessage = {actionCreators.clearErrorMessage}
            socialsAuth = {socialsAuth}
        />;
    }
}

export default connect((state) => ({ auth: state.auth }))(AuthContainer);


