import React from 'react';
import { Col, Row } from 'react-bootstrap';
import { Link } from 'react-router';
import Navigation from '../SearchNavigation';
import Shortstory from '../Shortstory';

/**
 * data - array of posts { alt_name: STRING, title: STRING, poster: STRING }
 * field - searching field: STRING
 * value - searching value: STRING
 * maxPage: INT
 * currentPage: INT
 */
export default class Content extends React.Component {
    constructor(props) {
        super(props);
        this.getPagination = this.getPagination.bind(this);
    }
    getPagination() {
        if (this.props.maxPage > 1)
            return (
                <div className = "pages">
                    <Navigation
                        maxPage = {this.props.maxPage}
                        currentPage = {this.props.currentPage}
                        link = {"/search/" + this.props.field + "/" + this.props.value + "/"}
                    />
                </div>
            );
        else
            return null;
    }
    render() {
        let content = this.props.data.map((val, i) => (
            <div className = "col-xs-12 col-sm-12 col-md-6 col-lg-4 col-xl-3" key = {i}>
                <div className = "row">
                    <Shortstory full_link = {"/post/"  + val.alt_name} title = {val.title} poster = {val.poster} />
                </div>
            </div>
        ));
        let statusString = 'Поиск по имени: ' + this.props.h1_title;
        if (this.props.field == 'category') {
            statusString = 'Поиск по категории: ' + this.props.h1_title;
        }
        return(
            <div className = "block">
                <h1 className="main-header">
                    <Link to = {"/search/" + this.props.field + "/" + this.props.value}>
                        {statusString}
                    </Link>
                </h1>
                <div className = "col-xs-12 col-sm-12">
                    <div className = "row">
                        {content}
                    </div>
                </div>
                {this.getPagination()}
            </div>
        );
    }
}


