import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Series from '../../components/PostPage/series';
import * as ActionCreators from './actions';

class SeriesContainer extends React.Component {
    render() {
        const actionCreators = bindActionCreators(ActionCreators, this.props.dispatch);;
        return this.props.post.data &&
        <Series
            series = {this.props.post.data.series}
            state = {this.props.series}
            actions = {actionCreators}
        />;
    }
}

export default connect((state) => ({ post: state.post, series: state.series }))(SeriesContainer);



