import Api from './lib/Api';
import ReduxThunk from 'redux-thunk';
import { PAGES } from './constants';

export * from './containers/Categories/actions';
export * from './containers/RandomRelease/actions';
export * from './containers/Friends/actions';
export * from './containers/Post/actions';
export * from './containers/Posts/actions';
export * from './containers/Page/actions';
export * from './containers/Error/actions';
export * from './containers/SearchField/actions';
export * from './containers/Series/actions';
export * from './containers/HomePage/actions';
import * as QSF from './containers/QuickSearchForm/actions';
import * as Auth from './containers/Auth/actions';

export {
    QSF,
    Auth,
};

export function loadData(uri, page, callBack) {
    return dispatch => {
        // dispatch(changePage(PAGES.LOADING));
        Api.get(uri)
            .then(r => {
                callBack(dispatch, r);
                // dispatch(changePage(page));
            })
            .catch(e => {
                console.error('There is an error', e);
                // dispatch(receiveError(e.message));
                // dispatch(changePage(PAGES.ERROR));
            });
    };
}


