import React from 'react';
import ReactLoading from 'react-loading';

export default class Loading extends React.Component {
    render() {
        return (
            <center>
                <ReactLoading
                    type="spinningBubbles"
                    color="#333"
                    delay={500}
                />
            </center>
        );
    }
}



