import React from 'react';
import Shortstory from "../Shortstory";
import { Col, Row, Alert } from 'react-bootstrap';
import Navigation from '../SearchNavigation';
import { Link } from 'react-router';
// import SearchForm from '../sidebar/search';
import Content from './content';
import Error from './error';
import Main from './main';
import DocumentMeta from 'react-document-meta';

export default class Search extends React.Component {
    render() {
        let searchName = this.props.params.field == 'category' ? this.props.data.category.name :this.props.data.postName;
        
        let title_tag = [];
        title_tag.push(searchName);
        if (this.props.params.field != 'category') {
            title_tag.push(`Поиск`);
        }
        if (this.props.data.currentPage > 1) {
            title_tag.push(`Страница ${this.props.data.currentPage}`);
        }
        title_tag.push(`FireDub.Net`);
        title_tag = title_tag.join(' - ');

        const meta = {
            title: title_tag,
            meta: {
                charset: 'utf-8',
                name: {
                    description: 'FireDub, FireDub.Net, firedub, firedub.net, firedub net, фаердаб, Аниме онлайн, скачать аниме бесплатно, аниме сезон, лучшее аниме, смотреть лучшее аниме, аниме 2 сезон, аниме 1 сезон, бесплатное скачать аниме, смотреть аниме онлайн бесплатно отаку, онгоинг',
                    keywords: 'firedub, firedub.net, firedub net, фаердаб, аниме онлайн, скачать аниме бесплатно, аниме сезон, лучшее аниме, смотреть лучшее аниме, аниме 2 сезон, аниме 1 сезон, бесплатное скачать аниме, смотреть аниме онлайн бесплатно отаку, онгоинг'
                }
            }
        };

        return (
            <section>
                <DocumentMeta {...meta} />
                {
                    this.props.data.posts.length > 0 ?
                    <Content
                        data = {this.props.data.posts}
                        field = {this.props.params.field}
                        value = {this.props.params.value}
                        maxPage = {this.props.data.maxPage}
                        currentPage = {this.props.data.currentPage}
                        h1_title = {searchName}
                    />
                    :
                    <Error value = {this.props.params.value} field = {this.props.params.field} />
                }
            </section>
        );
    }
}


