import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, browserHistory, IndexRoute, Redirect } from 'react-router';

import Wrapper from "./components/Wrapper";
import HomePage from "./containers/HomePage";
import Full from "./containers/Post";
import Search from './containers/Posts';
import NoMatch from "./components/errors/NoMatch";
import Auth from './containers/Auth';
import About from './components/About';

import { Provider } from 'react-redux';
import { createStore, applyMiddleware, compose} from 'redux';
import { syncHistoryWithStore } from 'react-router-redux'
import thunkMiddleware from 'redux-thunk';
import { createLogger } from 'redux-logger';
import * as reducer from './reducers';
import * as actions from './actions';

// middleware that logs actions
const loggerMiddleware = createLogger({ predicate: (getState, action) => !(process.env.NODE_ENV === 'production') });
// __DEV__

function configureStore() {
    const enhancer = compose(
        applyMiddleware(
            thunkMiddleware, // lets us dispatch() functions
            loggerMiddleware,
        ),
    );
    return createStore(reducer.reducers, reducer.initialStates, enhancer);
}

const store = configureStore();

// Create an enhanced history that syncs navigation events with the store
const history = syncHistoryWithStore(browserHistory, store)

// initial events
store.dispatch(actions.loadFriends());
store.dispatch(actions.loadCategories());
store.dispatch(actions.loadRandomRelease());
store.dispatch(actions.loadCategoriesFromHomePage());
store.dispatch(actions.Auth.hiddenCheckLogin());


/**
 * @todo
 * Add redirect from old links using <Redirect to = 'new-path' />
 * Doc: https://github.com/ReactTraining/react-router/blob/master/packages/react-router/docs/api/Redirect.md
 */

class Application extends React.Component {
    render() {
        return (
            <Provider store={this.props.store}>
                <Router history = {history}>
                    <Route path = "/" component = {Wrapper}>
                        <IndexRoute component = {HomePage} />
                        <Route path = "post/:alt_name" component = {Full} />
                        <Route path = "search/:field/:value(/page/:page_num)" component = {Search} />
                        <Route path = "profile/*" component = {Auth} />
                        <Route path = "about/*" component = {About} />
                        <Route path = "*" exact render = {() => <Redirect push to = '/' />} />
                    </Route>
                </Router>
            </Provider>
        );
    }
}

ReactDOM.render(<Application store = {store} />, document.getElementById('app'));


