import createReducer from '../../lib/createReducer';
import { RECEIVE_FRIENDS } from '../../constants';

const initialState = {
    data: [],
    isLoaded: false,
};

let reducer = createReducer(initialState, {
    [RECEIVE_FRIENDS]: (state, action) => {
        return {
            data: action.friends,
            isLoaded: true,
        };
    },
});

export { initialState, reducer };


