import Api from '../../lib/Api';
import { RECEIVE_CATEGORIES } from '../../constants';

export function loadCategories() {
    return dispatch => {
        const localDataKey = `categories`;
        const localData = Api.getLocalData(localDataKey); // do we have local data?
        if (localData) {
            dispatch(receiveCategories(localData));
        } else {
            const localDataOld = Api.getLocalData(localDataKey, true); // now let ignore deadline
            if (localDataOld) {
                dispatch(receiveCategories(localDataOld));
            }
            Api.get('category/all/')
                .then(r => {
                    dispatch(receiveCategories(r));
                    Api.saveLocalData(localDataKey, r); // lets update local storage!
                })
                .catch(e => {
                    if (localDataOld === null) {
                        dispatch(receiveCategories([]));
                    }
                });
        }
    };
}

export function receiveCategories(categories) {
    return {
        type: RECEIVE_CATEGORIES,
        categories,
    };
}


