import React from 'react';
import { Col, Row } from 'react-bootstrap';
import SearchForm from '../../containers/QuickSearchForm';

export default class Main extends React.Component {
    render() {
        return(
            <div className = "block">
                <Row>
                    <Col xs = {12} sm = {12}>
                        <center>
                            <h1>Поиск</h1>
                        </center>
                    </Col>
                    <Col xs = {12} sm = {8} smOffset = {2}>
                        <Row>
                            <div className="form-group">
                                <Col xs = {4} sm = {4} className = "text-right">
                                    <label for="name">Поиск по имени:</label>
                                </Col>
                                <Col xs = {8} sm = {8}>
                                    <Row>
                                        <SearchForm />
                                    </Row>
                                </Col>
                            </div>
                        </Row>
                    </Col>
                </Row>
            </div>
        );
    }
}


