import createReducer from '../../lib/createReducer';
import { HOME_PAGE } from '../../constants';

const initialState = [
    { name: "Анонсы", iconName: "list", category: "new_anime", data: [], isFetching: false, },
    { name: "Новые аниме", iconName: "star", category: "anime", data: [], isFetching: false, },
    { name: "Новые дорамы", iconName: "star", category: "drama", data: [], isFetching: false, },
    { name: "Новые фильмы", iconName: "star", category: "films", data: [], isFetching: false, },
];

let reducer = createReducer(initialState, {
    [HOME_PAGE.SAVE.CATEGORY]: (state, action) => {
        return state.map(obj => {
            if (obj.category != action.category) {
                return Object.assign({}, obj);
            }
            return Object.assign({}, obj, {
                data: action.posts,
                isFetching: false,
            });
        });
    },
    [HOME_PAGE.FETCHING]: (state, action) => {
        return state.map(obj => {
            if (obj.category != action.category) {
                return Object.assign({}, obj);
            }
            return Object.assign({}, obj, {
                isFetching: true,
            });
        });
    },
});

export { initialState, reducer };


