import createReducer from '../../lib/createReducer';
import { RECEIVE_POST, FETCHING_POST } from '../../constants';

const initialState = {
    data: null,
    isFetching: false,
};

let reducer = createReducer(initialState, {
    [RECEIVE_POST]: (state, action) => {
        return {
            data: action.post,
            isFetching: false,
        };
    },
    [FETCHING_POST]: (state, action) => {
        return Object.assign({}, state, {
            isFetching: true,
        });
    },
});

export { initialState, reducer };


