/**
 * @class
 */
class Api {
    /**
     * @return {Object} With keys from we can get data - local storage (new or old) or via request (uri)
     */
    static getConsts() {
        return {
            STORAGE: {
                NEW: 'API.STORAGE.NEW',
                OLD: 'API.STORAGE.OLD',
            },
            URI: 'API.URI'
        };
    }

    /**
     * 
     * @param {string} uri Uri to send request if not found in currect storage
     * @param {string} dataKey Key to find data into local storage
     * @param {function(mixed, string)} successCallback Will call after succesfully reqiesting or getting from storage. 1 param - received data, 2 param - from received.
     * @param {function(Object)} errorCallback Will call if there is trouble and we can't get data from server. Param - error object.
     */
    static getWithStorage(uri, dataKey, successCallback, errorCallback) {
        let localData = Api.getLocalData(localDataKey);
        let localDataOld = Api.getLocalData(localDataKey, true); // now let ignore deadline

        if (localData) {
            successCallback(localData, this.getConsts().STORAGE.NEW);
        } else {
            Api.get(uri)
                .then(r => {
                    successCallback(localData, this.getConsts().URI);
                })
                .catch(e => {
                    if (localDataOld) {
                        successCallback(localDataOld, this.getConsts().STORAGE.OLD);
                    } else {
                        errorCallback(e);
                    }
                });
        }
    }

    static saveLocalData(key, data, storeDaysCount = 1) {
        localStorage.setItem(key, JSON.stringify({
            savedDate: Date.now(),
            storeDaysCount,
            data
        }));
    }

    static getLocalData(key, ignoreDeadline = false) {
        let val = localStorage.getItem(key);
        if (val === null) {
            // no existing value
            return val;
        }
        val = JSON.parse(val);
        
        // is it old data? i mean - old functionality
        let isOldData = (val.savedDate <= 1508071491000 ? true : false);
        if (isOldData) {
            localStorage.removeItem(key);
            return null;
        }

        if (!ignoreDeadline) {
            const millisecsInDay = 24 * 60 * 60 * 1000;

            let savedDateInDays = val.savedDate;

            const storeDaysCountInMillisecs = millisecsInDay * val.storeDaysCount
            const deadline = savedDateInDays + storeDaysCountInMillisecs;

            if (Date.now() > deadline) {
                return null;
            }
        }
        return val.data;
    }

    static removeLocalData(key) {
        localStorage.removeItem(key);
    }

    static headers() {
        return {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'dataType': 'json',
        }
    }

    static get(route) {
        return this.xhr(route, null, 'GET');
    }

    static put(route, params) {
        return this.xhr(route, params, 'PUT')
    }

    static post(route, params) {
        return this.xhr(route, params, 'POST')
    }

    static delete(route, params) {
        return this.xhr(route, params, 'DELETE')
    }

    static xhr(route, params, verb) {
        const host = '/rest/';
        const url = host + route;
        let options = Object.assign({ method: verb, credentials: 'include' }, params ? { body: JSON.stringify(params) } : null );
        options.headers = Api.headers();
        return fetch(url, options)
            .then(response => {
                setTimeout(() => null, 0); // workaround for issue-6679 for react - native
                let json = response.json();
                if (response.status >= 200 && response.status < 300) {
                    return json;
                }
                return json.then( err => {
                    throw {
                        code: response.status,
                        message: err,
                    };
                });
            })
            .catch(err => {
                throw err;
            });
    }
}
export default Api;



