import createReducer from '../../lib/createReducer';
import { CHANGE_SEARCH_FIELD } from '../../constants';

const initialState = '';

let reducer = createReducer(initialState, {
    [CHANGE_SEARCH_FIELD]: (state, action) => {
        return action.title;
    },
});

export { initialState, reducer };


