import React from 'react';
import { Modal, Col, Row, Alert } from 'react-bootstrap';
import Top from './top';
import Fields from './fields';
import Left from './left';
import Series from '../../containers/Series';
import E_mess from '../Errors/E_mess';
import { Link } from 'react-router';
import DocumentMeta from 'react-document-meta';

export default class Full extends React.Component{
    getNormalElement() {
        let posterurl = (this.props.post.poster && this.props.post.poster.indexOf('<img src=') != -1) ? this.props.post.poster.replace('<img src="http://4anime.su', "").replace('<img src="', "").replace('" />', "") : "";
        if (posterurl == "" && this.props.post.poster)
            posterurl = this.props.post.poster;
        let categories = (
            <ul className = "breadcrumb">
                {this.props.post.category.map((v, i) => 
                    <li key = {v.id} className = "breadcrumb-item">
                        <Link to = {"/search/category/" + v.alt_name}>
                            {v.name}
                        </Link>
                    </li>
                )}
            </ul>
        );

        return (
            <div className = "fullstory">
                <Row>
                    <Col xs = {12} sm = {12}>
                        <Top title = {this.props.post.title} />
                    </Col>
                </Row>
                <Row>
                    <Col xs = {12} sm = {12} id="fstory-film">
                        {categories}
                    </Col>
                </Row>
                <Row>
                    <Left trailer_url = {this.props.post.trailer} poster = {posterurl} />
                    <div className = "col-xs-12 col-sm-12 col-md-12 col-lg-8">
                        <Series />
                    </div>
                </Row>
                <Row>
                    <Col xs = {12} sm = {12}>
                        <Fields item = {this.props.post} />
                    </Col>
                </Row>
            </div>
        );
    }

    render() {
        let docTitle = this.props.post.title;
        let docDescr = this.props.post.full_story.replace(/<[^>]+>/g,'');
        let docKeywds = 'firedub, firedub.net, firedub net, фаердаб, аниме онлайн, скачать аниме бесплатно, аниме сезон, лучшее аниме, смотреть лучшее аниме, аниме 2 сезон, аниме 1 сезон, бесплатное скачать аниме, смотреть аниме онлайн бесплатно отаку, онгоинг';

        const meta = {
            title: `${docTitle} - FireDub.Net`,
            meta: {
                charset: 'utf-8',
                name: {
                    description: docDescr,
                    keywords: docKeywds
                }
            }
        };
        return(
            <section>
                <DocumentMeta {...meta} />
                {this.getNormalElement()}
            </section>
        );
    }
}


