import React from 'react';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

export default class User extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdownOpen: false,
        };
        this.toggle = this.toggle.bind(this);
        this.getCorrectDate = this.getCorrectDate.bind(this);
        this.renderLabel = this.renderLabel.bind(this);
    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen,
        });
    }

    getCorrectDate(timeInt) {
        const date = new Date(parseInt(timeInt) * 1000);
        const dateStr = `${date.getDate()}.${date.getMonth() + 1}.${date.getFullYear()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
        return dateStr;
    }

    renderLabel(title, value) {
        return (
            <div className="form-group row">
                <label htmlFor="staticEmail" className="col-sm-5 col-form-label">{title}</label>
                <div className="col">
                    <p className="form-control-plaintext">{value}</p>
                </div>
            </div>
        );
    }

    render() {
        return (
            <div className="row">
                <div className = "col-xs-12 col-sm-5">
                    <div className="card">
                        <div className="card-body">
                            <h2>Личный кабинет</h2>
                            {this.renderLabel('Ваш никнейм', this.props.data.name)}
                            {this.renderLabel('Ваш email', this.props.data.email)}
                            {!!this.props.data.fullname && this.renderLabel('Ваше реальное', this.props.data.fullname)}
                            {this.renderLabel('Дата регистрации', this.getCorrectDate(this.props.data.reg_date))}
                            {this.renderLabel('Дата последней авторизации', this.getCorrectDate(this.props.data.lastdate))}
                            <button className="btn btn-danger" onClick={this.props.logout}>Выход</button>
                        </div>
                    </div>
                </div>
                <div className = "col">
                    <div className="card">
                        <div className="card-body">
                            <h2>Избранные сериалы</h2>
                            <div className="alert alert-warning">
                                На данный момент находится в разработке
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


