import createReducer from '../../lib/createReducer';
import { SERIES } from '../../constants';

const initialState = {
    activeType: null,
    activeNumber: 0,
    activePlayer: 0,
};

let reducer = createReducer(initialState, {
    [SERIES.CHANGE_TYPE]: (state, action) => {
        return Object.assign({}, state, {
            activeType: action.activeType,
            activeNumber: 0,
            activePlayer: 0,
        });
    },
    [SERIES.CHANGE_SERIA]: (state, action) => {
        return Object.assign({}, state, {
            activeNumber: action.activeNumber,
            activePlayer: 0,
        });
    },
    [SERIES.CHANGE_PLAYER]: (state, action) => {
        return Object.assign({}, state, {
            activePlayer: action.activePlayer,
        });
    },
});

export { initialState, reducer };


