import React from 'react';
import { Col, Row } from 'react-bootstrap';
import Field from './field';
import FieldDescription from './fieldDescription';
import { Link } from 'react-router';

export default class Fields extends React.Component{
    render(){
        let item = this.props.item;
        let N = 0;
        let fields = [];

        // if (item.title)
        //     fields.push(["Название", item.title, "time"]);
        
        if (item.year)
            fields.push(["Выпуск", item.year, "year"]);
        
        if (item.voicers)
            fields.push(["Русская озвучка", item.voicers, "voicers"]);
        
        if (item.translaters)
            fields.push(["Русский перевод", item.translaters, "translaters"]);
        
        if (item.timer)
            fields.push(["Таймер", item.timer, "timer"]);

        if (item.type)
            fields.push(["Тип", item.type, "time"]);
        
        if (item.author)
            fields.push(["Режиссер", item.author, "author"]);
        
        if (item.seriesCount)
            fields.push(["Колличество серий", item.seriesCount, "series_count"]);
        
        if (item.manga)
            fields.push(["Манга", item.manga, "manga"]);
        
        if (item.editor)
            fields.push(["Сценарист", item.editor, "editor"]);
        
        if (item.roles)
            fields.push(["В ролях", item.roles, "in_roles"]);
        
        let fieldsDom = fields.map((val, i) => {
            N++;
            return <Field
                key = {i}
                name = {val[0]}
                title = {val[1]}
                svg = {val[2]}
                n = {N} />;
        });

        return(
            <div>
                <FieldDescription description = {item.full_story} />
                {fieldsDom}
            </div>
        );
    }
}


