import Api from '../../lib/Api';
import { HOME_PAGE } from '../../constants';
import { initialState } from './reducers';

export function loadCategoriesFromHomePage() {
    return dispatch => {
        for (let obj of initialState) {
            const localDataKey = `HomePagePosts-${obj.category}`;
            const localData = Api.getLocalData(localDataKey);
            if (localData) {
                dispatch(receivePostsOnHomePage(localData.posts, localData.category));
            } else {
                const localDataOld = Api.getLocalData(localDataKey, true); // now let ignore deadline
                if (localDataOld) {
                    dispatch(receivePostsOnHomePage(localDataOld.posts, localDataOld.category));
                } else {
                    dispatch(fetchingCategoriesFromHomePage(obj.category));
                }
                Api.get(`search/category/${obj.category}`)
                    .then(r => {
                        dispatch(receivePostsOnHomePage(r.posts, obj.category));
                        Api.saveLocalData(localDataKey, {
                            posts: r.posts,
                            category: obj.category,
                        }, 1/290);
                    })
                    .catch(e => {
                        if (localDataOld === null) {
                            dispatch(receivePostsOnHomePage([], obj.category));
                        }
                    });
            }
        }
    };
}

export function fetchingCategoriesFromHomePage(category) {
    return {
        type: HOME_PAGE.FETCHING,
        category,
    };
}

export function receivePostsOnHomePage(posts, category) {
    return {
        type: HOME_PAGE.SAVE.CATEGORY,
        category,
        posts,
    };
}


