import { combineReducers } from 'redux';
import * as posts from './containers/Posts/reducers';
import * as post from './containers/Post/reducers';
import * as series from './containers/Series/reducers';
import * as error from './containers/Error/reducers';
import * as categories from './containers/Categories/reducers';
import * as friends from './containers/Friends/reducers';
import * as page from './containers/Page/reducers';
import * as searchField from './containers/SearchField/reducers';
import * as randomRelease from './containers/RandomRelease/reducers';
import * as HomePage from './containers/HomePage/reducers';
import * as QuickSearchForm from './containers/QuickSearchForm/reducers';
import * as Auth from './containers/Auth/reducers';
import { syncHistoryWithStore, routerReducer } from 'react-router-redux'

let reducers = combineReducers({
    posts: posts.reducer,
    post: post.reducer,
    series: series.reducer,
    error: error.reducer,
    categories: categories.reducer,
    friends: friends.reducer,
    page: page.reducer,
    search_title: searchField.reducer,
    randomRelease: randomRelease.reducer,
    latestPosts: HomePage.reducer,
    QSF: QuickSearchForm.reducer,
    auth: Auth.reducer,
    routing: routerReducer,
});

let initialStates = {
    posts: posts.initialState,
    post: post.initialState,
    series: series.initialState,
    error: error.initialState,
    categories: categories.initialState,
    friends: friends.initialState,
    page: page.initialState,
    search_title: searchField.initialState,
    randomRelease: randomRelease.initialState,
    latestPosts: HomePage.initialState,
    QSF: QuickSearchForm.initialState,
    auth: Auth.initialState,
}

export { reducers, initialStates };


