import * as constants from './constants';
import Api from '../../lib/Api';

export function register(data) {
    return dispatch => {
        Api.post('user/', data)
            .then((r) => {
                dispatch(registerSuccess());
                dispatch(login(data.username, data.userpass));
            })
            .catch(e => {
                dispatch(registerFailed(e.message));
            });
    }
}

export function hiddenCheckLogin() {
    return dispatch => {
        Api.post('user/auth/')
            .then((r) => {
                console.log(r);
                dispatch(loginSuccess(r));
            })
            .catch(e => {
                dispatch(loginFailed(null));
            });
    }
}

export function checkLogin() {
    return dispatch => {
        Api.post('user/auth/')
            .then((r) => {
                console.log(r);
                dispatch(loginSuccess(r));
            })
            .catch(e => {
                dispatch(loginFailed(e.message));
            });
    }
}

export function registerSuccess() {
    return {
        type: constants.REGISTER.SUCCESS,
    };
}

export function registerFailed(errorMessage) {
    return {
        type: constants.REGISTER.FAILED,
        errorMessage,
    };
}

export function login(email, userpass) {
    return dispatch => {
        Api.post('user/auth/', { email, userpass, })
            .then(r => {
                dispatch(loginSuccess(r));
            })
            .catch(e => {
                dispatch(loginFailed(e.message));
            });
    }
}

export function loginViaSocial(networkType, data) {
    return dispatch => {
        Api.post(`user/auth/${networkType}/`, {'data': data})
            .then(r => {
                dispatch(loginSuccess(r));
            })
            .catch(e => {
                dispatch(loginFailed(e.message));
            });
    }
}

export function loginViaVk() {
    return dispatch => {
        VK.Auth.login(r => {
            if (r.session) {
                dispatch(loginViaSocial('vk', r.session));
            }
        }, 1 + 4194304);
    }
}

export function logout() {
    return dispatch => {
        Api.post('user/exit/')
            .then(r => {
                dispatch(logoutSuccess());
            })
            .catch(e => {
                dispatch(logoutFailed(e.message));
            });
    }
}

export function loginSuccess(userData) {
    return {
        type: constants.LOGIN.SUCCESS,
        userData,
    };
}

export function loginFailed(errorMessage) {
    return {
        type: constants.LOGIN.FAILED,
        errorMessage,
    };
}

export function logoutSuccess() {
    return {
        type: constants.LOGOUT.SUCCESS,
    };
}

export function logoutFailed(errorMessage) {
    return {
        type: constants.LOGOUT.FAILED,
        errorMessage,
    };
}

export function clearErrorMessage() {
    return {
        type: constants.CLEAR_ERROR_MESSAGE,
    };
}


