import React from 'react';
import { Modal, Button, Col, Row, Alert } from 'react-bootstrap';

export default class FieldDescription extends React.Component{
    render(){
        return(
            <div className="info-block fullstory-description">
                <h2>
                    Описание:
                </h2>
                <div className = "row">
                    <div className = "col fs-body" dangerouslySetInnerHTML={{__html: this.props.description}}>
                    </div>
                </div>
            </div>
        );
    }
}


