import * as constants from './constants';
import Api from '../../lib/Api';

export function loadPosts(title) {
    return dispatch => {
        console.log(title);
        let uri = `search/name/${title}/1/10/`;

        let localDataKey = `QuickSearchForm-${title}`;
        let localData = Api.getLocalData(localDataKey);
        let localDataOld = Api.getLocalData(localDataKey, true); // now let ignore deadline

        if (localData) {
            dispatch(receivePosts(localData));
        } else {
            Api.get(uri)
                .then(r => {
                    dispatch(receivePosts(r.posts));
                    Api.saveLocalData(localDataKey, r.posts);
                })
                .catch(e => {
                    if (localDataOld) {
                        dispatch(receivePosts(localDataOld));
                    } else {
                        dispatch(receivePosts([]));
                    }
                });
        }
    };
}

export function receivePosts(posts) {
    return {
        type: constants.RECEIVE_POSTS,
        posts,
    };
}

export function resetPosts() {
    return receivePosts([]);
}



