import React from 'react';
import { Link, browserHistory } from 'react-router';

export default class Search extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            _value: '',
        };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        let value = event.target.value;
        this.setState({_value: value});
        this.props.actions.loadPosts(value);
    }

    render() {
        let clearResultBtn = <li className = "text-center search-clear" onClick = {this.props.actions.resetPosts.bind(this)}>Закрыть</li>;
        let data = null;
        if (this.props.posts.length > 0) {
            data = this.props.posts.map(
                (v, i) => (
                    <li key = {i}>
                        <Link to = {'/post/' + v.alt_name}>
                            {v.title}
                        </Link>
                    </li>
                )
            );
            data.push(clearResultBtn);
        }
        return(
            <div className="form-inline">
                <div id = "search" className = "input-group">
                    <input className = "form-control" type = "text" placeholder = "Искать на сайте" value = {this.state._value} onChange = {this.handleChange} />
                    <span className = "input-group-btn">
                        <button onClick = {() => {browserHistory.push('/search/name/' + this.state._value);}} className = "btn btn-outline-success" type = "button" title = "Найти">
                            <span className = "fa fa-search"></span>
                        </button>
                    </span>
                    <ul>
                        {data}
                    </ul>
                </div>
            </div>
        );
    }
}


