import React from 'react';
import { Col, Row, Alert } from 'react-bootstrap';

export default class E_mess extends React.Component{

    render(){
        return(
            <Row>
                <Col xs = {12} sm = {12}>
                    <center>
                        <Alert bsStyle = "danger">Ошибка: <b>{this.props.error}</b></Alert>
                    </center>
                </Col>
            </Row>
        );
    }

}


