import Api from '../../lib/Api';
import { RECEIVE_FRIENDS } from '../../constants';

export function loadFriends() {
    return dispatch => {
        const localDataKey = `friends`;
        const localData = Api.getLocalData(localDataKey);
        if (localData) {
            dispatch(receiveFriends(localData));
        } else {
            const localDataOld = Api.getLocalData(localDataKey, true); // now let ignore deadline
            if (localDataOld) {
                dispatch(receiveFriends(localDataOld));
            }
            Api.get('friends/')
                .then(r => {
                    dispatch(receiveFriends(r));
                    Api.saveLocalData(localDataKey, r);
                })
                .catch(e => {
                    if (localDataOld === null) {
                        dispatch(receiveFriends([]));
                    }
                });
        }
    };
}

export function receiveFriends(friends) {
    return {
        type: RECEIVE_FRIENDS,
        friends,
    };
}


