import { CHANGE_PAGE, PAGES } from '../../constants';

export function changePage(page) {
    return {
        type: CHANGE_PAGE,
        page,
    };
}

export function resetPage() {
    return {
        type: CHANGE_PAGE,
        page: PAGES.HOME,
    };
}


