import React from 'react';
import { Button, Form, FormGroup, Label, Input, FormText, Col } from 'reactstrap';

export default class LoginBlock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: "",
            password: "",
        };
        this.changedNickname = this.changedNickname.bind(this);
        this.changedPassword = this.changedPassword.bind(this);
    }

    changedNickname(event) {
        this.setState({
            email: event.target.value,
        });
    }
    
    changedPassword(event) {
        this.setState({
            password: event.target.value,
        });
    }

    submit(event) {
        this.props.login(this.state.email, this.state.password);
        event.preventDefault();
    }

    render() {
        return (
            <Form>
                <FormGroup>
                    <Label for="input-email">Почта</Label>
                    <Input type="email" name="email" id="input-email" placeholder="email@google.com" value={this.state.nickname} onChange={this.changedNickname} />
                </FormGroup>
                <FormGroup>
                    <Label for="input-password">Пароль</Label>
                    <Input type="password" name="password" id="input-password" placeholder="********" value={this.state.password} onChange={this.changedPassword} />
                </FormGroup>
                <FormGroup>
                    <Button onClick={this.submit.bind(this)} color="primary">Войти</Button>
                </FormGroup>
            </Form>
        );
    }
}


