import { SERIES } from '../../constants';

export function changeSeriesType(activeType) {
    return {
        type: SERIES.CHANGE_TYPE,
        activeType,
    };
}

export function changeSeriesNumber(activeNumber) {
    return {
        type: SERIES.CHANGE_SERIA,
        activeNumber,
    };
}

export function changeSeriesPlayer(activePlayer) {
    return {
        type: SERIES.CHANGE_PLAYER,
        activePlayer,
    };
}


