import React from 'react';

export default class Svg extends React.Component{
    render(){
        const descr = (this.descr) ? this.descr : "Ваш браузер не поддерживает SVG.";
        return(
            <object type = "image/svg+xml" data = {"/images/" + this.props.url}>
                {descr}
            </object>
        );
    }
}


