import React from 'react';
import { connect } from 'react-redux';
import PostPage from '../../components/PostPage';
import Loading from '../../components/Loading';
import { requestPost } from './actions';

class PostContainer extends React.Component {
    loadPost(altName) {
        if (this.props.post.isFetching) {
            return null;
        }
        if (this.props.post.data === null || this.props.post.data.alt_name != altName) {
            this.props.dispatch(requestPost(altName));
        }
    }

    componentWillMount() {
        this.loadPost(this.props.params.alt_name);
    }

    componentWillUpdate(next) {
        this.loadPost(next.params.alt_name);
    }

    render() {
        if (this.props.post.isFetching) {
            return <Loading />;
        }
        if (!this.props.post.data) {
            return null;
        }
        return <PostPage post = {this.props.post.data} />;
    }
}

export default connect((state) => ({ post: state.post }))(PostContainer);


