import React from 'react';
import { Button, Form, FormGroup, Label, Input, FormText, Col } from 'reactstrap';

export default class RegistrationBlock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            nickname: "",
            fullname: "",
            email: "",
            password: "",
            passwordRepeat: "",
        };
        this.changedNickname = this.changedNickname.bind(this);
        this.changedFullname = this.changedFullname.bind(this);
        this.changedEmail = this.changedEmail.bind(this);
        this.changedPassword = this.changedPassword.bind(this);
        this.changedPasswordRepeat = this.changedPasswordRepeat.bind(this);
    }

    changedNickname(event) {
        this.setState({
            nickname: event.target.value,
        });
    }
    
    changedFullname(event) {
        this.setState({
            fullname: event.target.value,
        });
    }
    
    changedEmail(event) {
        this.setState({
            email: event.target.value,
        });
    }
    
    changedPassword(event) {
        this.setState({
            password: event.target.value,
        });
    }
    
    changedPasswordRepeat(event) {
        this.setState({
            passwordRepeat: event.target.value,
        });
    }

    submit(event) {
        const data = {
            username: this.state.nickname,
            userpass: this.state.password,
            email: this.state.email,
            fullname: this.state.fullname,
        };
        this.props.register(data);
        event.preventDefault();
    }

    render() {
        return (
            <Form>
                <FormGroup onSubmit={this.submit.bind(this)}>
                    <Label for="input-nickname">Nickname</Label>
                    <Input type="text" name="nickname" id="input-nickname" placeholder="TokiSeven" value={this.state.nickname} onChange={this.changedNickname} />
                </FormGroup>
                <FormGroup>
                    <Label for="input-fullname">Ваше имя</Label>
                    <Input type="text" name="fullname" id="input-fullname" placeholder="Владимир Рябцев" value={this.state.fullname} onChange={this.changedFullname} />
                </FormGroup>
                <FormGroup>
                    <Label for="input-email">Email</Label>
                    <Input type="email" name="email" id="input-email" placeholder="firedub@email.com" value={this.state.email} onChange={this.changedEmail} />
                </FormGroup>
                <FormGroup>
                    <Label for="input-password">Пароль</Label>
                    <Input type="password" name="password" id="input-password" placeholder="********" value={this.state.password} onChange={this.changedPassword} />
                </FormGroup>
                <FormGroup>
                    <Label for="input-password-repeat">Повторите пароль</Label>
                    <Input valid={this.state.password == this.state.passwordRepeat} type="password" name="password-repeat" id="input-password-repeat" placeholder="********" value={this.state.passwordRepeat} onChange={this.changedPasswordRepeat} />
                </FormGroup>
                <FormGroup>
                    <Button onClick={this.submit.bind(this)} color="success">Регистрация</Button>
                </FormGroup>
            </Form>
        );
    }
}


