import React from 'react';
import { Col } from 'react-bootstrap';
import { Link } from 'react-router';

class NavigationItem extends React.Component{
    render(){
        let additionalClass = this.props.active === true ? "active" : "";
        return(
            <li className = {`page-item ${additionalClass}`}>
                <Link to = {`${this.props.link}page/${this.props.number}`} className = "page-link">
                    {this.props.number}
                </Link>
            </li>
        );
    }
}

export default class Navigation extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            currentPage: 1,
            maxPage: 0
        }
    }

    render(){
        let navigation = (<div></div>);

        if (this.props.maxPage > 1) {
            navigation = [];
            for (let i = 1; i <= this.props.maxPage; i++){
                let active = (this.props.currentPage == i) ? true : false;
                navigation.push(
                    <NavigationItem
                        key = {i}
                        active = {active}
                        number = {i}
                        link = {this.props.link}
                    />
                );
            }
        }

        return(
            <nav aria-label="Навигация по поиску">
                <ul className="pagination justify-content-center">
                    {navigation}
                </ul>
            </nav>
        );
    }
}


